import React, {Component} from 'react';


export default class Grid extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: props.gridArray,
            countRows: 10,
            countColumn: 32,
        };
    }

    makeGrid(){
        let grid = [];
        let key = 0;

        for(let i = 0;i < this.state.grid[0].length;i++){
            for(let j = 0;j < this.state.grid.length;j++){
                key++;
                grid.push(
                    <div className="cell" key={ key }>
                        {this.state.grid[i][j]}
                    </div>
                );
            }
        }


        return grid;
    }

    render(){
        let grid = this.makeGrid();
        return(
            <div className="grid">
                {
                   grid
                }
            </div>
        );
    }
}